import React from 'react';
import Link from 'gatsby-link';

import './TermsLink.scss';

const TermsLink = () => {
    return (
        <div className="termsLink__container copytext-extra-small">
            {'By clicking you agree to our '}
            <Link to={'/terms/'} >
                Terms of Service
            </Link>
            { ' and ' }
            <Link to={'/privacy/'} >
                Privacy Policy.
            </Link>
        </div>
    );
}

export default TermsLink;



